@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
*,*::before,*::after{box-sizing:border-box}html{font-size:100%}body{background-color:#1b1b1b;padding:0;min-height:100vh;margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Open Sans' 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}


nav{padding:20px}@media (max-width: 600px){nav{padding:10px}}ul{list-style-type:none;margin:0;padding:0}a{position:relative;text-decoration:none;color:#ffffff}a:before{content:"";position:absolute;height:3px;right:0px;bottom:-3px;left:0px;background-color:#d81dd8}

